<template>
    <button class="tizi-button ellipsis" :class="[size,type]">
    <slot />
  </button>
</template>

<script>
export default {
    name:'TiButton',
    props: {
        size: {
            type:String,
            default:'middle'
        },
        type: {
            type:String,
            default:'default'
        }
    }
}
</script>

<style lang="scss" scoped>
.tizi-button {
    // 渲染一个按钮风格
    appearance: none;
    border: none;
    outline: none;
    background: #fff;
    text-align: center;
    border: 1px solid transparent;
    border-radius: 4px;
    // 鼠标指针效果
    cursor: pointer;
}
.large {
  width: 240px;
  height: 50px;
  font-size: 16px;
}
.middle {
  width: 180px;
  height: 50px;
  font-size: 16px;
}
.small {
  width: 100px;
  height: 32px;
  font-size: 14px;  
}
.mini {
  width: 60px;
  height: 32px;
  font-size: 14px;  
}
.default {
  border-color: #e4e4e4;
  color: #666;
}
.gray {
  border-color: #ccc;
  background: #ccc;;
  color: #fff;
}

</style>